
.Footer {
    background: white;
    width: 100%;
    height: 50px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    z-index: 1001;
    left:0;
    bottom:0;
    right:0;
    position: fixed;
}

.Footer ul {
    display: -ms-inline-flexbox;
    display: inline-flex;
    margin-top: 15px;
    list-style: none;
    color: black;
    font-size: 10px;
}

a.LinkStyles {
    color: black;
    margin-left: 15px;
    margin-right: 15px;
    text-decoration: none;
    font-size: 12px;
}

a.LinkStyles:hover {
    color: blue;
}


