.Outer {
    height: 100%;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    background: url(../../images/background.jpg);
    background-repeat: no-repeat;
    background-size: 100%;
    padding: 50px 0 0;
}

.OuterContent {
    height: 100%;
    width: 100%;
    background:  rgba(255,255,255,0.1);
}

.AboutMe {
    width: 100%;
    height: 100px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}

.AboutMe h2 {
    text-shadow: 5px 0 5px  black;
    font-size: 85px;
    display: -ms-flexbox;
    display: flex;
    margin: 0;
    -ms-flex-pack: center;
        justify-content: center;
    color: white;
}

.OuterMobile {
    height: 100%;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    background: url(../../images/background.jpg);
    background-repeat: repeat;
    background-size: 100%;
    padding: 50px 0 0;
}

.OuterContentMobile {
    height: 100%;
    width: 95%;
    background:  rgba(255,255,255,0.1);
}

.AboutMeMobile {
    width: 100%;
    height: 100px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}

.AboutMeMobile h2 {
    text-shadow: 5px 0 5px  black;
    font-size: 45px;
    display: -ms-flexbox;
    display: flex;
    margin: 0;
    -ms-flex-pack: center;
        justify-content: center;
    color: white;
}


